.hero
	margin: 0 -24px 60px
	&__slider
		position: relative
		height: 0 
		min-height: 1px
		padding-bottom: 44.345%
	&__slide
		position: relative
		height: 100% 
	&__slide-titles
		position: absolute 
		top: 90px
		left: 60px
		max-width: 550px	
	&__slide-title
		color: #000
		font-size: 56px
		line-height: 68px  
		font-weight: 700
		margin-bottom: 15px 
		&_white
			color: #fff
	&__slide-subtitle
		font-size: 18px
		line-height: 28px
		&_white
			color: #fff
	&__slide-img
		img
			max-width: 100%
			width: 100%
			height: auto

.btn-nav
	position: absolute
	bottom: 51px
	left: 44%
	transform: translateX(-50%) 
	display: inline-block
	vertical-align: middle
	margin: 6px 10px 0
	background: none
	border: none
	box-shadow: none
	border-radius: 0
	text-indent: 10000px
	overflow: hidden
	+s('stop')
	&.is-paused
		+s('play')

.products
	margin: 0 -12px 45px
	font-size: 0
	&__col
		display: inline-block
		vertical-align: top
		width: 25%
		margin-bottom: 25px
		padding: 0 12px

.product
	position: relative
	display: block
	height: 200px
	background-color: #f7f7f7
	padding: 25px 32px 22px
	overflow: hidden
	text-decoration: none
	+clr
	transition: opacity 0.2s
	&:active
		color: #000
	&__title,
	&__descr
		position: relative
		z-index: 2
	&__title
		margin-bottom: 12px
		font-size: 20px
		line-height: 1.2
		color: #000
		font-weight: bold
	&__descr
		margin-bottom: 20px
		font-size: 16px
		color: #363636
		line-height: 1.3
	&__btns
		position: relative
		z-index: 2
		display: flex
		flex-flow: row nowrap
		justify-content: space-between
		align-items: center
	&__btn
		display: inline-block
		width: calc(50% - 5px)
		height: 30px
		line-height: 30px
		text-decoration: none
		text-align: center
		border: 1px solid black
		border-radius: 15px
		background: none
		font-size: 12px
		text-transform: uppercase
		color: #363636
		transition: background 0.2s
		&:first-child
			margin-right: 10px
		&:hover
			background: rgba(0,0,0,0.05)
	&__img
		position: absolute


.videos

.main-videos

.main-video
	max-height: 798px

.product-video
	position: relative
	padding-bottom: 56.5%
	height: 0
	width: 100%
	margin-bottom: 15px
	iframe
		position: absolute
		top: 0
		left: 0
		width: 100%
		height: 100%

.videothumbs
	margin: 0 -10px
	font-size: 0

.videothumb
	position: relative
	display: inline-block
	height: 200px
	width: 25%
	padding: 0 10px
	